import React, { Component } from 'react';
import './App.css';
import { Button, Row, Col, Upload, Icon } from 'antd';
import Header from "./components/Header"
import "antd/dist/antd.css";
import OpusMediaRecorder from 'opus-media-recorder'
// opus-media-recorder options
const workerOptions = {
  encoderWorkerFactory: function () {
    return new Worker(process.env.PUBLIC_URL + '/opus-media-recorder/encoderWorker.umd.js')
  },
  OggOpusEncoderWasmPath: process.env.PUBLIC_URL + '/opus-media-recorder/OggOpusEncoder.wasm',
  WebMOpusEncoderWasmPath: process.env.PUBLIC_URL + '/opus-media-recorder/WebMOpusEncoder.wasm',
};

let FileSaver = require('file-saver');
const hasGetUserMedia = !!(navigator.mediaDevices.getUserMedia || navigator.getUserMedia || navigator.webkitGetUserMedia ||
  navigator.mozGetUserMedia || navigator.msGetUserMedia);

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      recordAudio: null,
      src: null,
      url: null,
      data: null,
      micEnabled: false,
      recordFlag: true,
      playFlag: true,
      audio: new Audio(),
      file: false
    };



  }
  uploadProps = {
    name: "audio",
    accept: ".opus",
    beforeUpload: file => {
      console.log(file)
      this.setState({ data: file, url: URL.createObjectURL(file), file: false })
    }

  }

  componentDidMount = () => {
    if (!hasGetUserMedia) {
      alert("Browser not supported")
      return;
    }
    this.requestUserMedia()
    this.state.audio.addEventListener("ended", () => {
      this.stopAudio()
    })
    let setDuration = (duration) => {
      console.log(duration)
      this.setState({ duration })
    }
    // let duration = 0;
    this.state.audio.addEventListener("loadeddata", function () {
      let duration = this.duration
      setDuration(duration)
    });
  }


  requestUserMedia = async () => {
    console.log('requestUserMedia')
    try {
      await navigator.mediaDevices.getUserMedia({ audio: true });
      window.MediaRecorder = OpusMediaRecorder;
      this.setState({ micEnabled: true })
    } catch (err) {
      alert("Enable microphone and refresh " + err)
      this.setState({ micEnabled: false })
    }
  }


  startRecording = async () => {
    this.requestUserMedia()
    if (!this.state.playFlag) {
      this.stopAudio()
    }
    this.setState(prevState => ({
      recordFlag: !prevState.recordFlag,
      playFlag: true,
      data: null
    }))
    navigator.mediaDevices.getUserMedia({ audio: true }).then(stream => {
      const options = { mimeType: 'audio/ogg; codecs=opus' }
      this.recorder = new MediaRecorder(stream, options, workerOptions);
      this.setState({ state: 'in' });
      this.recorder.start();

      this.recorder.addEventListener('dataavailable', (e) => {
        console.log('Recording stopped, data available');
        this.onDataAvailable(e);
      });
      this.recorder.addEventListener('start', (e) => {
        console.log('start');
        this.setState({ state: 'recording' });
      })
      this.recorder.addEventListener('stop', (e) => {
        console.log('stop');
        this.setState({ state: 'in' });
      })
      this.recorder.addEventListener('pause', (e) => {
        console.log('pause');
        this.setState({ state: 'paused' });
      })
      this.recorder.addEventListener('resume', (e) => {
        console.log('resume');
        this.setState({ state: 'recording' });
      })
      this.recorder.addEventListener('error', (e) => {
        console.log('error');
      })
    });

  }

  onDataAvailable = (e) => {
    this.setState({
      data: e.data
    })
    this.setState({
      url: URL.createObjectURL(this.state.data)
    })
    const audio = this.state.audio
    audio.src = this.state.url
    this.setState({ audio })
  }

  stopRecording = async () => {
    this.requestUserMedia();
    this.setState(prevState => ({
      recordFlag: !prevState.recordFlag,
      file: !prevState.file
    }))
    this.recorder.stop()
  }

  playAudio = async () => {
    this.setState(prevState => ({
      playFlag: !prevState.playFlag
    }))
    const audio = this.state.audio
    audio.src = this.state.url
    audio.play();
    this.setState({ audio })
  }




  stopAudio = () => {
    this.setState(prevState => ({
      playFlag: !prevState.playFlag
    }))
    const audio = this.state.audio
    audio.pause()
    audio.currentTime = 0;
    this.setState({ audio })

  }

  saveAudio = () => {
    FileSaver.saveAs(this.state.data, "audio.opus");
  }


  render() {
    let playButton, recordButton, saveButton;
    // Disable if mic is not enabled
    if (!this.state.micEnabled) {
      recordButton = <Button type="secondary" size="large" onClick={this.startRecording} disabled style={{ marginBottom: "20px" }} >Start Recording</Button>
    }
    // Switch between start and stop button
    else if (this.state.recordFlag) {
      recordButton = <Button type="primary" size="large" onClick={this.startRecording} style={{ marginBottom: "20px" }} >Start Recording</Button>
    } else {
      recordButton = <Button type="primary" size="large" onClick={this.stopRecording} style={{ marginBottom: "20px" }} >Stop Recording</Button>
    }

    // Only display if there is data available
    if (this.state.data && (this.state.recordFlag && this.state.playFlag)) {
      playButton = <Button type="primary" size="large" onClick={this.playAudio}  >Play {this.state.duration}s Audio</Button>
    } else if (this.state.data && (this.state.recordFlag && !this.state.playFlag)) {
      playButton = <Button type="danger" size="large" onClick={this.stopAudio}  >Stop Playing Audio</Button>
    }
    if (this.state.file) {
      saveButton = <Button type="primary" size="large" onClick={this.saveAudio}  >Save Audio</Button>
    }
    return (
      <div className="App">
        {Header()}
        <Row style={{ marginTop: "50px" }}>
          <Col span={24}>
            {recordButton}
          </Col>
        </Row>
        <Row>
          <Col span={8}>
            {playButton}
          </Col>
          <Col span={8}>
            {saveButton}
          </Col>
          <Col span={8}><Upload {...this.uploadProps}>
            <Button>
              <Icon type="upload" /> Click to Upload
          </Button>
          </Upload></Col>
        </Row>
        <br />
      </div>
    );
  }
}

export default App;
